import React from 'react'
import PropTypes from 'prop-types'
import {connect} from 'react-redux'
import actions from '../../actions'
import styled from 'styled-components'

const Div = styled.div`
    font-size:36px;
    margin-bottom: 5vh;
    display: ${props => props.pageCount<=1 ? 'none' : 'flex'};
    flex-direction: row;
    justify-content:center;
    align-items:center;
    width:600px;
`

const Button = styled.button`
    background-color: white;
    font-size:36px;
    border-radius:5px;
    border-left:none;
    border-right:none;
    border-top: 1px solid #3ACFD5;
    border-bottom: 1px solid #3a4ed5;
    background-position: 0 0, 100% 0;
    background-repeat: no-repeat;
    -webkit-background-size: 1px 100%;
    -moz-background-size: 1px 100%;
    background-size: 1px 100%;
    background-image: url(data:image/svg+xml;base64,),url(data:image/svg+xml;base64,);
    background-image: -webkit-linear-gradient(top, #3acfd5 0%, #3a4ed5 100%), -webkit-linear-gradient(top, #3acfd5 0%, #3a4ed5 100%);
    background-image: -moz-linear-gradient(top, #3acfd5 0%, #3a4ed5 100%), -moz-linear-gradient(top, #3acfd5 0%, #3a4ed5 100%);
    background-image: -o-linear-gradient(top, #3acfd5 0%, #3a4ed5 100%), -o-linear-gradient(top, #3acfd5 0%, #3a4ed5 100%);
    background-image: linear-gradient(to bottom, #3acfd5 0%, #3a4ed5 100%), linear-gradient(to bottom, #3acfd5 0%, #3a4ed5 100%);
    &:focus, &:active {
        outline:none;
    }
`

const Prev = styled.div`
    margin:50px;
    width: 86px;
    height:45px;
`

const Page = ({response, title, page, searchMovies, pageCount, isLoading}) => {
    if(isLoading) return null
    if(response==='False')  return null
    if(!title) return null

    const handleButton = (type) => {
        if(type==='next'&&page<pageCount){
            searchMovies({
                title: title,
                page: page+1
            })
        }
        if(type==='prev'&&page>1){
            searchMovies({
                title: title,
                page: page-1
            })
        }
    }

    return (
        <Div pageCount={pageCount}>
            <Prev>
                {page===1 ? null : <Button onClick={()=>handleButton('prev')}>Prev</Button>}
            </Prev>
            <span>{page}</span>
            <Prev>
                {page===pageCount ? null : <Button onClick={()=>handleButton('next')}>Next</Button>}
            </Prev>
        </Div>
    )
}

Page.propTypes = {
    title: PropTypes.string,
    pageCount: PropTypes.number,
    searchMovies: PropTypes.func.isRequired,
    page: PropTypes.number
}

export default connect(
    state => ({
        title: state.title,
        page: state.page,
        pageCount: Math.round(state.totalResults/10),
        isLoading: state.isLoading,
        response: state.Response
    }),
    {
        searchMovies: actions.searchMovies
    }
)(Page)